@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@400;700&display=swap);
:root {
  --primary-color: #0aa9e7;
  --secondary-color: #424242;
  --background-color: #fbfbfb;
  --font-color: white;

  --max-width: 900px;
}

html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: 'Oswald', sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* background-color: var(--background-color); */
  background: radial-gradient(circle, rgba(221,221,221,1) 0%, rgba(255,255,255,1) 100%); 
}



* {
  box-sizing: border-box;
}

td.question-board-cell {
    cursor: pointer;
    width: 100px;
    height: 150px;
    text-align: center;
    padding: 10px;
    vertical-align: middle;
    background-color: var(--primary-color);
    border-radius: 2px;
    color: var(--font-color);
    border: 3px solid white;

    transition: 0.5s transform;
}

td.question-board-cell:hover {
    transform: scale(97%);
}

td.question-board-cell.is-revealed {
    background-color: var(--secondary-color);
}
.question-board-title {
    text-align: center;
    font-size: 2rem;
    margin: 0 auto;
    color: var(--font-color);
    background-color: var(--secondary-color);
    max-width: var(--max-width);
    border: 3px solid white;
    border-bottom: none;
}

table {
    width: 100%;
    max-width: var(--max-width);
    margin: 0 auto;

    font-size: 1.5rem;

    border-collapse: collapse;
}

table th {
    z-index: 10;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    color: var(--font-color);
    background-color: var(--secondary-color);
}

.question-card {
    background-color: transparent;
    perspective: 1000px;
    max-width: var(--max-width);
    height: 500px;

    margin: 0 auto;
    font-size: 2rem;
}

.question-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    transition: transform 0.8s;
    transform-style: preserve-3d;
    border: 3px solid white;
}

.question-card.flipped .question-card-inner {
    transform: rotateX(180deg);
}

.question-card-front, .question-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    color: var(--font-color);

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    padding: 1em;
}

.question-card-front h6, .question-card-back h6 {
    margin: 0;
}

.question-card-front {
    background-color: var(--primary-color);
}

.question-card-back {
    background-color: var(--secondary-color);
    transform: rotateX(180deg);
}

.question-card-content {
    flex: 1 1;
    margin: 0;
    font-size: 4rem;
    text-align: center;
}
